.card-drink {
    padding: 5px;
    height: 100px;
    width: 140px;
    box-shadow: 2px 2px 5px rgb(63, 63, 63);
    overflow: hidden;
    text-align: center;
    cursor: pointer;
    transition: .1s;
    position: relative;
    margin: 0;
    flex: 1 1 20%;

    &:active {
        transform: translate(3px, 3px);
        box-shadow: 2px 2px 5px grey;
        transition: .1s;
    }

    img{
        height: 70px;
    }

    figcaption {
        .price {
            position: absolute;
            top: 10px;
            right: 10px;
            background: #007bff;
            border-radius: 3px;
            padding: 8px;
            color: white;
        }
        .devise {
            font-size: smaller;
            margin-left: 2px;
        }
    }
}