:root {
    --white: #fff;
    --darkblue: #007bff;
    --lightblue: #d0e0e7;
  }

.bill {
    background: #f2f3f5;
    width: 365px;
    border-radius: 4px;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-right: 30px;

    .table-wrapper {
        overflow: auto;
        text-align: left;

        table{
            width: 100%;
        }
    }

    .table-wrapper::-webkit-scrollbar {
        height: 8px;
    }

    .table-wrapper::-webkit-scrollbar-thumb {
        background: var(--darkblue);
        border-radius: 40px;
    }

    .table-wrapper::-webkit-scrollbar-track {
        background: var(--white);
        border-radius: 40px;
    }

    .table-wrapper table {
        margin: 50px 0 20px;
        border-collapse: collapse;
    }

    .table-wrapper table th,
    .table-wrapper table td {
        padding: 10px;
    }

    .table-wrapper table th {
        color: var(--white);
        background: var(--darkblue);
    }

    .table-wrapper table tbody tr:nth-of-type(even) > * {
        background: var(--lightblue);
    }

    .table-wrapper table td:first-child {
        max-width: 15px;
    }
    .table-wrapper table td:nth-child(2) {
        min-width: 120px;
    }

    .bill_total{
        display: flex;
        justify-content: space-between;
        background: var(--darkblue);
        color: white;
        font-weight: 700;
        font-size: 20px;
        padding: 10px;
    }

    .delete-row {
        text-align: center;
    }

    .delete-btn {
        cursor: pointer;
        background: #dc3545;
        color: white;
        height: 22px;
        width: 22px;
        border: none;
        font-weight: 700;
        box-shadow: 1px 1px 2px rgb(63, 63, 63);
        transition: .1s;

        &:active {
            box-shadow: -1px -1px 2px rgb(63, 63, 63);
            transition: .1s;
        }
    }
}
